import { EnumListType } from "../DocStructure/EnumListStyle";

export const guid = (() => {
  const pool = new Set();
  const generate = () => {
    return Math.floor((1 + Math.random()) * 0x1000000)
      .toString(16)
      .substring(1);
  };
  return () => {
    let current = generate();
    while (pool.has(current)) {
      current = generate();
    }
    pool.add(current);
    return current;
  };
})();

export const isChinese = (word: string): boolean => {
  const charCode = word.charCodeAt(0);
  return (0x4E00 <= charCode && charCode <= 0x9FA5) ||  // 	20902
    (0x9FA6 <= charCode && charCode <= 0x9FEF) ||       // 	74
    (0x3400 <= charCode && charCode <= 0x4DB5) ||       // A	6582
    (0x20000 <= charCode && charCode <= 0x2A6D6) ||     // B	42711
    (0x2A700 <= charCode && charCode <= 0x2B734) ||     // C	4149
    (0x2B740 <= charCode && charCode <= 0x2B81D) ||     // D	222
    (0x2B820 <= charCode && charCode <= 0x2CEA1) ||     // E	5762
    (0x2CEB0 <= charCode && charCode <= 0x2EBE0) ||     // F	7473
    (0x2F00 <= charCode && charCode <= 0x2FD5) ||       // 	214
    (0x2E80 <= charCode && charCode <= 0x2EF3) ||       // 	115
    (0xF900 <= charCode && charCode <= 0xFAD9) ||       // 	477
    (0x2F800 <= charCode && charCode <= 0x2FA1D) ||     // 	542
    (0xE815 <= charCode && charCode <= 0xE86F) ||       // PUA(GBK)	81
    (0xE400 <= charCode && charCode <= 0xE5E8) ||       // 	452
    (0xE600 <= charCode && charCode <= 0xE6CF) ||       // PUA	207
    (0x31C0 <= charCode && charCode <= 0x31E3) ||       // 	36
    (0x2FF0 <= charCode && charCode <= 0x2FFB) ||       // 	12
    (0x3105 <= charCode && charCode <= 0x312F) ||       // 	43
    (0x31A0 <= charCode && charCode <= 0x31BA) ||       // 	22
    (0xFF01 <= charCode && charCode <= 0xFF5E) ||       // 
    (0x3007 <= charCode && charCode <= 0x3011) ||       // 
    charCode === 0x2013 ||      //   
    charCode === 0x2014 ||      //   
    // 
    // charCode === 0x2018 ||      //   
    // charCode === 0x2019 ||      // 
    // charCode === 0x201C ||      //   
    // charCode === 0x201D ||      // 
    charCode === 0x2026 ||      //   
    charCode === 0x3000 ||      // 
    charCode === 0x3001 ||      //   
    charCode === 0x3002 ||      //   
    charCode === 0x3014 ||      //   
    charCode === 0x3015;        // 
};

export const isScriptWord = (word: string): boolean => {
  return !(
    isChinese(word)
  );
};

export const splitIntoBat = (
  array: any[],
  splitter: (currentValue?: any, previousValue?: any, index?: number, array?: any[]) => boolean,
  includeSplitter: boolean = false): any[] => {
  const bat: any[] = [];
  let cache: any[] = [];
  for (let index = 0; index < array.length; index++) {
    const currentValue = array[index];
    cache.push(currentValue);
    const previousValue = index > 0 ? array[index - 1] : undefined;
    if (splitter(currentValue, previousValue, index, array)) {
      if (!includeSplitter) {
        cache.pop();
      }
      bat.push(cache);
      cache = [];
    }
  }
  if (cache.length > 0) {
    bat.push(cache);
  }
  return bat;
};

export const calListTypeFromChangeData = (changeData: string): EnumListType => {
  switch (changeData) {
    case "decimal":
      return EnumListType.ol_1;
      break;
    case "ckj-decimal":
      return EnumListType.ol_2;
      break;
    case "upper-decimal":
      return EnumListType.ol_3;
      break;
    case "circle":
      return EnumListType.ul_1;
      break;
    case "ring":
      return EnumListType.ul_2;
      break;
    case "arrow":
      return EnumListType.ul_3;
      break;
    default:
      throw new Error('unknown list type');
  }
};

export const convertTo26 = (num: number, upperCase = false) => {
  const offset = upperCase ? 64 : 96;
  let str = "";
  while (num > 0) {
    let m = num % 26;
    if (m === 0) {
      m = 26;
    }
    str = String.fromCharCode(m + offset) + str;
    num = (num - m) / 26;
  }
  return str;
};

/**
 * 
 */
export const numberToChinese = (() => {
  const chnNumChar = ["", "", "", "", "", "", "", "", "", ""];
  const chnUnitSection = ["", "", "", "", ""];
  const chnUnitChar = ["", "", "", ""];

  const sectionToChinese = (section: number) => {
    let strIns = '';
    let chnStr = '';
    let unitPos = 0;
    let zero = true;
    while (section > 0) {
      const v = section % 10;
      if (v === 0) {
        if (!zero) {
          zero = true;
          chnStr = chnNumChar[v] + chnStr;
        }
      } else {
        zero = false;
        strIns = chnNumChar[v];
        strIns = chnUnitChar[unitPos];
        chnStr = strIns + chnStr;
      }
      unitPos++;
      section = Math.floor(section / 10);
    }
    return chnStr;
  };

  return (num: number) => {
    let unitPos = 0;
    let strIns = '';
    let chnStr = '';
    let needZero = false;

    if (num === 0) {
      return chnNumChar[0];
    }

    while (num > 0) {
      const section = num % 10000;
      if (needZero) {
        chnStr = chnNumChar[0] + chnStr;
      }
      strIns = sectionToChinese(section);
      strIns += (section !== 0) ? chnUnitSection[unitPos] : chnUnitSection[0];
      chnStr = strIns + chnStr;
      needZero = (section < 1000) && (section > 0);
      num = Math.floor(num / 10000);
      unitPos++;
    }

    return chnStr;
  };
})();

export const convertToRoman = (() => {
  const aArray = [1000, 900, 500, 400, 100, 90, 50, 40, 10, 9, 5, 4, 1];
  const upperArray = ["M", "CM", "D", "CD", "C", "XC", "L", "XL", "X", "IX", "V", "IV", "I"];
  const lowerArray = ["m", "cm", "d", "cd", "c", "xc", "l", "xl", "x", "ix", "v", "iv", "i"];
  return (num: number, upperCase = false) => {
    const strArray = upperCase ? upperArray : lowerArray;
    let str = "";
    for (let i = 0; i < aArray.length; i++) {
      while (num >= aArray[i]) {
        str += strArray[i];
        num -= aArray[i];
      }
    }
    return str;
  };
})();

export const calListItemTitle = (type: EnumListType, indent: number, index: number, parentTitle: string): string => {
  switch (type) {
    case EnumListType.ol_1:
      return calOl1title(indent, index);
    case EnumListType.ol_2:
      return calOl2title(indent, index);
    case EnumListType.ol_3:
      return calOl3title(index, parentTitle);
    case EnumListType.ul_1:
      return calUl1title(indent);
    case EnumListType.ul_2:
      return '';
    case EnumListType.ul_3:
      return calUl3title(indent, index);
  }
};

const calOl1title = (indent: number, index: number): string => {
  index++;
  switch (indent % 3) {
    case 0:
      return index + '.';
    case 1:
      return convertTo26(index) + '.';
    case 2:
      return convertToRoman(index) + '.';
    default:
      return '';
  }
};

const calOl2title = (indent: number, index: number): string => {
  if (indent === 0) {
    return numberToChinese(index + 1) + '';
  }
  switch (indent % 3) {
    case 1:
      return convertTo26(index + 1) + ')';
    case 2:
      return convertToRoman(index + 1) + '.';
    case 0:
      return (index + 1) + '.';
    default:
      return '';
  }
};

const calOl3title = (index: number, parentTitle: string): string => {
  return parentTitle + (index + 1) + '.';
};

const calUl1title = (indent: number): string => {
  if (indent === 0) {
    return '';
  }
  switch (indent % 3) {
    case 1:
      return '';
    case 2:
      return '';
    case 0:
      return '';
    default:
      return '';
  }
};

const calUl3title = (indent: number, index: number): string => {
  if (indent === 0) {
    return '';
  }
  switch (indent % 3) {
    case 1:
      return '';
    case 2:
      return '';
    case 0:
      return '';
    default:
      return '';
  }
};
